import React, { useState } from 'react';
import axios from 'axios';
import '../../styles/Idsearch.css';

export default function IdSearch() {
  const [userEmail, setEmail] = useState('');
  const [userId, setUserid] = useState('');
  const [searchResult, setSearchResult] = useState('');

  const idCheckHandler = () => {
    const password = {
      email: userEmail,
      userid: userId,
    };

    axios
      .post('/api/member/search/password', password)
      .then((response) => {
        console.log(response.data);
        const responseData = response.data;
        setSearchResult(responseData);
        alert(' .');
      })

      .then((result) => {
        console.log(result);
      })

      .catch((error) => {
        console.log(error);
        alert('     .');
      });
  };

  return (
    <div className="wrap">
      <div className="box">
        <p className="play">
          PLAY <span className="ground">GROUND</span>
        </p>
        <div className="emailinput">    .</div>

        <input
          className="idinput"
          onSubmit={idCheckHandler}
          type="text"
          id="userId"
          placeholder=" ."
          onChange={(e) => setUserid(e.target.value)}
        />

        <input
          className="idinput1"
          onSubmit={idCheckHandler}
          type="email"
          id="userEmail"
          placeholder=" ."
          onChange={(e) => setEmail(e.target.value)}
        />

        <button className="idsearch2" onClick={idCheckHandler}>
           
        </button>
        <br />
        <a className="searchresult">{searchResult.randomPassword}</a>
        <br></br>
        <a href="/Signin">  </a>
      </div>
    </div>
  );
}
