import React, { useState } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import '../../styles/App.css';

export default function SignUp() {
  const navigate = useNavigate();
  const [userId, setUserid] = useState(''); //  
  const [userEmail, setEmail] = useState(''); //   
  const [userName, setName] = useState(''); //  
  const [userNickname, setUserNickname] = useState(''); //   
  const [userPassword, setPassword] = useState(''); //   
  const [userPasswordCheck, setPasswordCheck] = useState(''); //   
  const [userCurriculm, setCurriculum] = useState(''); //   
  const [checkId, setCheckId] = useState('');
  const theme = createTheme();

  const idCheck = () => {
    const reid = /^[a-zA-z0-9]{4,12}$/;
    if (userId.match(reid) === null) {
      alert('   .');
      return;
    }

    axios
      .post('/api/member/checkid', { userid: userId })
      .then((response) => {
        console.log(response);
        if (response.data.responseMessage.includes('USERID_USEABLE')) {
          alert('  .');
          setCheckId(true);
        } else {
          alert(' .  .');
          setCheckId(false);
          setUserid('');
        }
      })
      .catch((error) => {
        console.log(error);
        alert('   .');
      });
  };

  const signUpHandler = () => {
    const regl = /^[A-Za-z0-9]{8,20}$/;
    //     
    if (userPassword !== userPasswordCheck) {
      alert('  .');
      return;
    }
    if (userPassword.match(regl) === null) {
      alert('   .    8  . ');
      return;
    }

    const data = {
      userid: userId,
      password: userPassword,
      email: userEmail,
      name: userName,
      nickname: userNickname,
      curriculum: userCurriculm,
    };

    axios //  .
      .post('/api/member/signup', data)
      .then((response) => {
        console.log(response.data);

        alert(' .');

        navigate('/signin');
      }) // response .
      .catch((error) => {
        console.log(error);
        alert(' .');
      }); //  error .
  };

  return (
    <ThemeProvider theme={theme}>
      {/* <style>{styles}</style> */}
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <p className="play">
            PLAY <span className="ground">GROUND</span>
          </p>
          <div className="data">PLAY DATA    .</div>
          <Box component="form" noValidate onSubmit={signUpHandler} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={9}>
                <TextField
                  autoComplete="given-name"
                  name="Id"
                  required
                  fullWidth
                  id="Id"
                  label=""
                  autoFocus
                  onChange={(e) => setUserid(e.target.value)}
                />
              </Grid>
              <Button
                sx={{ mt: 2, mb: 1, marginLeft: '16px', marginTop: '20px' }}
                variant="contained"
                color="success"
                onClick={idCheck}
              >
                
              </Button>

              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="password"
                  label=""
                  name="password"
                  autoComplete="password"
                  type="password"
                  onChange={(e) => setPassword(e.target.value)}
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="passwordCheck"
                  label=" "
                  name="passwordCheck"
                  autoComplete="passwordCheck"
                  type="password"
                  onChange={(e) => setPasswordCheck(e.target.value)}
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label=""
                  name="email"
                  autoComplete="email"
                  type="email"
                  onChange={(e) => setEmail(e.target.value)}
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="name"
                  label=""
                  name="name"
                  autoComplete="name"
                  onChange={(e) => setName(e.target.value)}
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="nickname"
                  label=""
                  name="nickname"
                  autoComplete="nickname"
                  onChange={(e) => setUserNickname(e.target.value)}
                />
              </Grid>

              <Grid item xs={12}>
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label"></InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={userCurriculm}
                    label="Curriculm"
                    onChange={(e) => setCurriculum(e.target.value)}
                  >
                    <MenuItem value=""></MenuItem>
                    <MenuItem value=""></MenuItem>
                    <MenuItem value=""></MenuItem>
                    <MenuItem value=""></MenuItem>
                  </Select>
                </FormControl>
              </Grid>
            </Grid>

            <Button
              type="button"
              fullWidth
              variant="contained"
              color="secondary"
              sx={{ mt: 3, mb: 2 }}
              onClick={() => signUpHandler()}
            >
              {' '}
              
            </Button>

            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link href="/Signin" variant="body1">
                    ? 
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
