import { Button, Grid, Paper, TextField, Typography } from '@mui/material';
import Avvvatars from 'avvvatars-react';
import React, { useState } from 'react';
import axios from '../Token/Interceptor';

export const QnaComment = ({ comment }) => {
  const detailDate = (a) => {
    const milliSeconds = new Date() - a;
    const seconds = milliSeconds / 1000;
    if (seconds < 60) return ` `;
    const minutes = seconds / 60;
    if (minutes < 60) return `${Math.floor(minutes)} `;
    const hours = minutes / 60;
    if (hours < 24) return `${Math.floor(hours)} `;
    const days = hours / 24;
    if (days < 7) return `${Math.floor(days)} `;
    const weeks = days / 7;
    if (weeks < 5) return `${Math.floor(weeks)} `;
    const months = days / 30;
    if (months < 12) return `${Math.floor(months)} `;
    const years = days / 365;
    return `${Math.floor(years)} `;
  };

  const calcCommentDatetime = detailDate(new Date(comment.createdDate));

  const [isVisible, setIsVisible] = useState(false);
  const [commentContent, setCommentContent] = useState(comment.content);

  //  
  const onclickModifyHandler = () => {
    setIsVisible(!isVisible);
  };

  //  
  const onclickDeleteHandler = (contentId) => {
    axios
      .post(
        '/api/qna/answer/delete',
        {
          id: contentId,
        },
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .then((response) => {
        console.log(response.data);
        alert(' .');
      })
      .then((result) => {
        console.log(result);
      })
      .catch((error) => {
        console.log(error);
        alert('  .');
      })
      .finally(() => {
        window.location.reload();
      });
  };

  //   
  const onModifySaveHandler = () => {
    axios
      .post(
        '/api/qna/answer/modify',
        {
          id: comment.id,
          content: commentContent,
        },
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .then((response) => {
        console.log(response.data);
        alert(' .');
      })
      .then((result) => {
        console.log(result);
      })
      .catch((error) => {
        console.log(error);
        alert('  .');
      })
      .finally(() => {
        window.location.reload();
      });
  };

  return (
    <div>
      <Paper
        sx={{
          p: 1,
          margin: 'auto',
          marginBottom: '1%',
          maxWidth: '75%',
          flexGrow: 1,
          backgroundColor: (theme) => (theme.palette.mode === 'dark' ? '#1A2027' : '#fff'),
        }}
      >
        <Grid container spacing={4}>
          <Grid item xs={30} sm container>
            <Grid item xs className="flex justify-between">
              <div className="flex mt-2" style={{ textAlign: 'center', marginInline: '10px' }}>
                <Typography className="m-2" gutterBottom component="div">
                  <Avvvatars value={comment.userId} style="shape" size={30} />{' '}
                </Typography>
                <Typography
                  className="m-2"
                  gutterBottom
                  component="div"
                  style={{ fontSize: '14px', alignSelf: 'center' }}
                >
                  {' '}
                  {comment.nickname}{' '}
                </Typography>
                <Typography className="m-3" gutterBottom component="div" style={{ fontSize: '12px' }}>
                  {' '}
                  {calcCommentDatetime}{' '}
                </Typography>
              </div>

              {isVisible ? (
                <div className="flex" style={{ marginBlock: '10px' }}>
                  <Button
                    size="small"
                    color="primary"
                    variant="outlined"
                    onClick={() => {
                      onModifySaveHandler();
                    }}
                    sx={{ mr: '5px' }}
                  >
                    {' '}
                    {' '}
                  </Button>
                  <Button
                    size="small"
                    color="error"
                    variant="outlined"
                    onClick={() => setIsVisible(false)}
                    sx={{ mr: '5px' }}
                  >
                    {' '}
                    {' '}
                  </Button>
                </div>
              ) : comment.editable ? (
                <div className="flex" style={{ marginBlock: '10px' }}>
                  <Button
                    size="small"
                    color="primary"
                    variant="outlined"
                    onClick={() => onclickModifyHandler()}
                    sx={{ mr: '5px' }}
                  >
                    {' '}
                    {' '}
                  </Button>
                  <Button
                    size="small"
                    color="error"
                    variant="outlined"
                    onClick={() => onclickDeleteHandler(comment.id)}
                    sx={{ mr: '5px' }}
                  >
                    {' '}
                    {' '}
                  </Button>
                </div>
              ) : (
                <></>
              )}
            </Grid>
          </Grid>
        </Grid>
        <Grid style={{ marginInline: '10px', marginTop: '5px' }}>
          {isVisible ? (
            <div className="mb-4 pr-5 pl-11">
              <div className="w-full">
                <TextField
                  multiline
                  fullWidth
                  defaultValue={commentContent}
                  onChange={(e) => {
                    setCommentContent(e.target.value);
                  }}
                />
              </div>
            </div>
          ) : (
            <div className="mb-4 pr-5 pl-11">
              <Typography
                className="m-2"
                gutterBottom
                component="div"
                variant="body1"
                style={{ whiteSpace: 'pre-line', overflowWrap: "break-word" }}
              >
                {comment.content}
              </Typography>
            </div>
          )}
        </Grid>
      </Paper>
    </div>
  );
};

export default QnaComment;
