import React, { useState } from 'react';
import Header from '../Base/Header';
import Footer from '../Base/Footer';
import axios from '../Token/Interceptor';

import jobNameList from '../Data/Job';
import jobSkillList from '../Data/Skill';
import jobLocationList from '../Data/Location';
import {
  Autocomplete,
  Box,
  Button,
  CircularProgress,
  Dialog,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  List,
  ListItem,
  TextField,
} from '@mui/material';
import CancelIcon from '@mui/icons-material/Cancel';
import { useNavigate } from 'react-router-dom';

const Recommend = () => {
  const navigate = useNavigate();

  const [jobName, setJobName] = useState('');
  const [jobLocaction, setJobLocation] = useState('');
  const [jobSkill, setJobSkill] = useState('');
  const [jobSkillSet, setJobSkillSet] = useState([]);
  const [jobGrade, setJobGrade] = useState('');
  const jobGradeList = ['', '', ''];

  const [isLoading, setIsLoading] = useState(false);

  const addSkillHandler = () => {
    if (jobSkill === '' || jobGrade === '') {
      window.alert('   .');
      return;
    }

    if (jobSkillSet.some((item) => item.includes(jobSkill))) {
      window.alert('  / .');
      return;
    }

    const newSkill = jobSkill + '-' + jobGrade;

    if (newSkill.trim() !== '') {
      setJobSkillSet([...jobSkillSet, newSkill]);
      setJobSkill('');
      setJobGrade('');
    }
  };

  const removeSkillHandler = (index) => {
    const updatedSkills = jobSkillSet.filter((_, i) => i !== index);
    setJobSkillSet(updatedSkills);
  };

  // //       
  const recommendedDataHandler = () => {
    if (jobSkillSet.length < 3) {
      window.alert('/  3  .');
      return;
    } else if (jobSkillSet.length > 20) {
      window.alert('  /  . ( 20)');
      return;
    }

    setIsLoading(true);

    const skillSetDict = jobSkillSet.reduce((dict, item) => {
      let gradePoint = 0;

      if (item.split('-')[1] === '') {
        gradePoint = 1;
      } else if (item.split('-')[1] === '') {
        gradePoint = 2;
      } else if (item.split('-')[1] === '') {
        gradePoint = 3;
      }

      dict[item.split('-')[0]] = gradePoint;

      return dict;
    }, {});

    const inputData = {
      jobName: jobName,
      jobLocation: jobLocaction,
      jobSkills: skillSetDict,
    };

    console.log(inputData);

    axios
      .post(
        '/api/recommend/list',
        { inputData: inputData },
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .then((res) => {
        console.log(res.data.data);

        const result = JSON.parse(res.data.data);

        navigate('/recommend/result', { state: result });
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  return (
    <>
      <Header />
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          placeContent: 'center',
          alignItems: 'center',
          padding: 50,
          marginBottom: 200,
        }}
      >
        <div className="fs-1 fw-bold">  </div>

        <br></br>
        <p>       ?</p>

        <br></br>

        <div className="fw-bold"> /   ()</div>

        <br></br>

        <p>    / ! </p>

        <br></br>

        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            placeContent: 'center',
            alignItems: 'center',
          }}
        >
          <Autocomplete
            value={jobName}
            onChange={(event, value) => {
              setJobName(value);
            }}
            id="controllable-states-demo"
            options={jobNameList.map((option) => option.title)}
            sx={{ width: 300, margin: 1 }}
            renderInput={(params) => <TextField {...params} label="" placeholder="" />}
          />

          <Autocomplete
            value={jobLocaction}
            onChange={(event, value) => {
              setJobLocation(value);
            }}
            id="controllable-states-demo"
            options={jobLocationList.map((option) => option.title)}
            sx={{ width: 300, margin: 1 }}
            renderInput={(params) => <TextField {...params} label="" />}
          />
        </div>

        <br></br>

        <div className="fw-bold">
           /   <span style={{ color: 'red' }}>()</span>
        </div>

        <br></br>

        <div> /    . ( 3 )</div>

        <br></br>

        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            placeContent: 'center',
            alignItems: 'center',
          }}
        >
          <Autocomplete
            value={jobSkill}
            onChange={(event, value) => {
              setJobSkill(value);
            }}
            id="controllable-states-demo"
            options={jobSkillList.map((option) => option.title)}
            sx={{ width: 300, margin: 1 }}
            renderInput={(params) => <TextField {...params} label=" / " />}
          />

          <Autocomplete
            value={jobGrade}
            onChange={(event, value) => {
              setJobGrade(value);
            }}
            id="controllable-states-demo"
            options={jobGradeList}
            sx={{ width: 300, margin: 1 }}
            renderInput={(params) => <TextField {...params} label="" />}
          />
        </div>
        <Button variant="outlined" color="secondary" onClick={addSkillHandler}>
          / 
        </Button>

        <br></br>

        <div className="fw-bold">/  ( 20)</div>

        <br></br>

        <div style={{ width: '80%' }}>
          <List
            style={{
              display: 'flex',
              flexDirection: 'row',
              justifyContent: 'center',
              placeContent: 'center',
              alignItems: 'center',
            }}
          >
            <Grid container spacing={2} columns={{ xs: 4, sm: 8, md: 12 }}>
              {jobSkillSet.map((item, index) => (
                <ListItem key={index} sx={{ width: 'fit-content', placeItems: 'center' }}>
                  <Box component="span" sx={{ p: 1, paddingLeft: 2, borderRadius: 8, backgroundColor: '#3dbbb0' }}>
                    {item}
                    <IconButton
                      aria-label="delete"
                      size="small"
                      color="error"
                      sx={{ ':hover': { backgroundColor: 'transparent' } }}
                      onClick={() => removeSkillHandler(index)}
                    >
                      <CancelIcon fontSize="small" color="action" sx={{ paddingBottom: 0.2 }}></CancelIcon>
                    </IconButton>
                  </Box>
                </ListItem>
              ))}
            </Grid>
          </List>
        </div>

        <br></br>

        <Button variant="contained" color="success" className="btn btn-primary" onClick={recommendedDataHandler}>
            
        </Button>

        {isLoading && (
          <Dialog
            open={isLoading}
            onClose={() => {}}
            aria-labelledby="loading-dialog-title"
            disableBackdropClick
            disableEscapeKeyDown
          >
            <DialogTitle id="loading-dialog-title">  </DialogTitle>
            <DialogContent sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
              <CircularProgress color="secondary" />
            </DialogContent>
          </Dialog>
        )}
      </div>
      <Footer />
    </>
  );
};

export default Recommend;
